const createMessage = (id: string, senderId: string, chatId: string, content?: string, attachments?: Attachment[], minutesAgo: number = 0): Message => ({
  id,
  senderId,
  chatId,
  content,
  attachments,
  timestamp: Date.now() - minutesAgo * 60 * 1000,
  status: 'read',
});

